<template>
  <div>

    <el-row>
      <el-col :span="12">
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
          <el-tab-pane label="" name="first">
            <el-table
              :data="financial.cashflow.month"
              style="width: 35%">
              <el-table-column
                prop="in"
                label=""
                width="100">
              </el-table-column>
              <el-table-column
                prop="out"
                label=""
                width="100">
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="" name="second">
            <el-table
              :data="financial.cashflow.day"
              style="width: 35%">
              <el-table-column
                prop="in"
                label=""
                width="100">
              </el-table-column>
              <el-table-column
                prop="out"
                label=""
                width="100">
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="" name="third">
            <el-table
              :data="financial.cashflow.histories"
              style="width: 70%">
              <el-table-column
                prop="_id"
                label=""
                width="180"
                :formatter="dateFormater">
              </el-table-column>
              <el-table-column
                prop="in"
                label=""
                width="100">
              </el-table-column>
              <el-table-column
                prop="out"
                label=""
                width="100">
              </el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>
      </el-col>
      <el-col :span="12">
        <el-tabs v-model="activeNameOrder" type="card" @tab-click="handleClick">
          <el-tab-pane label="" name="first">
            <el-table
              :data="financial.order.statistics"
              style="width: 60%">
              <el-table-column
                prop="month"
                label=""
                width="180">
              </el-table-column>
              <el-table-column
                prop="day"
                label=""
                width="180">
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="" name="third">
            <el-table
              :data="financial.order.histories"
              style="width: 90%">
              <el-table-column
                prop="timestamp"
                label=""
                width="120"
                :formatter="dateFormater">
              </el-table-column>
              <el-table-column
                prop="userName"
                label=""
                width="100">
              </el-table-column>
              <el-table-column
                prop="finalAmount"
                label=""
                width="100">
              </el-table-column>
              <el-table-column
                prop="storeName"
                label=""
                width="100">
              </el-table-column>
              <el-table-column
                prop="coupon"
                label=""
                width="100">
              </el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>
      </el-col>
    </el-row>

    <el-row>
      <el-col :span="12">
        <el-tabs v-model="activeNameCoupon" type="card" @tab-click="handleClick">
          <el-tab-pane label="" name="first">
            <el-table
              :data="financial.coupon.statistics"
              style="width: 40%">
              <el-table-column
                prop="month"
                label=""
                width="100">
              </el-table-column>
              <el-table-column
                prop="day"
                label=""
                width="100">
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="" name="third">
            <el-table
              :data="financial.coupon.histories"
              style="width: 80%">
              <el-table-column
                prop="timestamp"
                label=""
                width="120"
                :formatter="dateFormater">
              </el-table-column>
              <el-table-column
                prop="userName"
                label=""
                width="100">
              </el-table-column>
              <el-table-column
                prop="coupon"
                label=""
                width="100">
              </el-table-column>
              <el-table-column
                prop="storeName"
                label=""
                width="100">
              </el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>

      </el-col>
      <el-col :span="12">
        <el-tabs v-model="activeNameSubsidy" type="card" @tab-click="handleClick">
          <el-tab-pane label="" name="first">
            <el-table
              :data="financial.subsidy.statistics"
              style="width: 40%">
              <el-table-column
                prop="month"
                label=""
                width="100">
              </el-table-column>
              <el-table-column
                prop="day"
                label=""
                width="100">
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="" name="third">
            <el-table
              :data="financial.subsidy.histories"
              style="width: 90%">
              <el-table-column
                prop="timestamp"
                label=""
                width="120"
                :formatter="dateFormater">
              </el-table-column>
              <el-table-column
                prop="storeName"
                label=""
                width="100">
              </el-table-column>
              <el-table-column
                prop="userName"
                label=""
                width="100">
              </el-table-column>
              <el-table-column
                prop="subsidy"
                label=""
                width="100">
              </el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>
      </el-col>
    </el-row>

    <el-tabs v-model="activeNameUser" type="card" @tab-click="handleClick">
      <el-tab-pane label="" name="first">
        <el-table
          :data="financial.user.statistics"
          style="width: 30%">
          <el-table-column
            prop="month"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="day"
            label=""
            width="180">
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="" name="third">
        <el-table
          :data="financial.user.histories"
          style="width: 80%">
          <el-table-column
            prop="createdAt"
            label=""
            width="180"
            :formatter="dateFormater">
          </el-table-column>
          <el-table-column
            prop="data.name"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="data.firstPayStoreName"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="data.payAmount"
            label=""
            width="180">
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>


  </div>
</template>

<script>
  import axios from 'axios'
  import moment from 'moment'
  export default {
    data() {
      return {
        activeName: 'first',
        activeNameOrder: 'first',
        activeNameCoupon: 'first',
        activeNameSubsidy: 'first',
        activeNameUser: 'first',
        financial: {},
        stores: [],
      }
    },
    methods: {
      handleClick() {

      },
      dateFormater: function(row, column) {
        var date = row[column.property];
        if (date == 0) {
          return ''
        }
        return moment(date).format("YYYY-MM-DD")
      },
    },
    mounted() {
      let data = {
        tp: 'financial'
      }
      axios.post('http://localhost:8090/proxy', data).then(resp => {
        this.financial = resp.data
        console.log(resp.data)
      })
    }
  }
</script>

<style>
</style>



