<template>

  <div>
    <el-table
        :data="plates"
        style="width: 100%">
        <el-table-column
          label=""
          width="200">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.title }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          width="200">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.index }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          width="200">
          <template slot-scope="scope">
            <span style="margin-left: 10px" v-if="scope.row.status === 1" ></span>
            <span style="margin-left: 10px" v-if="scope.row.status === 0" ></span>
          </template>
        </el-table-column>
        <el-table-column label="" width="500">
          <template slot-scope="scope">
            <el-button type="text" @click="edit(scope.row)"></el-button>
            <el-button type="text" @click="hide(scope.row, scope.$index)"></el-button>
            <el-button type="text" @click="show(scope.row, scope.$index)"></el-button>
          </template>
        </el-table-column>
      </el-table>

	  <el-dialog title="" :visible.sync="dialogFormVisible">

      <el-row>
        <el-col :span="2">
          <div class="title"></div>
        </el-col>
        <el-col :span="4">
          <el-input placeholder="" v-model="newPlate.title" clearable></el-input>
        </el-col>

        <el-col :span="2">
          <div class="title"></div>
        </el-col>
        <el-col :span="4">
          <el-input placeholder="" v-model="newPlate.index" clearable></el-input>
        </el-col>

        <el-col :span="8">

           <el-upload class="upload-demo" ref="newPlate" action="http://localhost:8090/upload" :on-success="onNewPlateImageUploadSuccess" :auto-upload="false">
             <el-button slot="trigger" size="small" type="primary"></el-button>
             <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload(10)"></el-button>
             <div slot="tip" class="el-upload__tip"></div>
           </el-upload>

        </el-col>
      </el-row>

      <el-row>
        <el-col :span="3">
          <div class="title"></div>
        </el-col>
        <el-col :span="4">
          <el-select v-model="newPlate.linkTo" filterable placeholder="">
              <el-option
                v-for="item in links"
                :key="item.id"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
        </el-col>
      </el-row>
	    <div slot="footer" class="dialog-footer">
	      <el-button @click="dialogFormVisible = false"> </el-button>
	      <el-button type="primary" @click="doAddNewPlate"> </el-button>
	    </div>
	  </el-dialog>

    <el-button type="primary" style="width: 100%;margin-top: 5px;" icon="el-icon-plus" @click="addPlate"></el-button>
  </div>
</template>

<script>
  import axios from 'axios'
  import { Loading } from 'element-ui';
  export default {
    data() {
      return {
        plates: [],
        dialogFormVisible: false,

        newPlate: {
          title: '',
          index: -1,
          image: '',
          linkTo: '',
        },

        links: [
          {
            value: '1',
            label: ''
          }, {
            value: '2',
            label: ''
          }, {
            value: '3',
            label: ''
          }, {
            value: '4',
            label: ''
          }, {
            value: '5',
            label: ''
          },
          {
            value: '6',
            label: ''
          }]
      }
    },
    methods: {
      doAddNewPlate() {
        if (!this.newPlate.title) {
          this.$message.error("")
          return
        }
        if (this.newPlate.index == -1) {
          this.$message.error("")
          return
        }
        if (!this.newPlate.image) {
          this.$message.error("")
          return
        }
        if (!this.newPlate.linkTo) {
          this.$message.error("")
          return
        }

        let loadingInstance = Loading.service({ fullscreen: true });
        this.newPlate.tp = 1
        axios.post('http://localhost:8090/addplate',this.newPlate).then(resp => {
          loadingInstance.close()
          if (resp.status != 200 ){
            this.$message.error("")
          }else {
            this.dialogFormVisible = false
            console.log(resp.data)
            this.plates.push({_id: resp.data._id, title: resp.data.title, index: resp.data.index, items: resp.data.items})
            this.$message.success("")
          }
          this.newPlate.title = ''
          this.newPlate.index = -1
          this.newPlate.image = ''
          this.newPlate.linkTo = ''
        })

      },

      onNewPlateImageUploadSuccess(e) {
        this.newPlate.image = e
      },


      edit(row) {
        this.$router.push({name: 'coupons', params: {_id: row._id}})
      },
      hide(row, index) {
        axios.post('http://localhost:8090/proxy',{tp: 'switchplatestatus',_id: row._id, status: 0}).then(resp => {
          if (resp.status == 200) {
            this.$message.success("")
            this.plates[index].status = 0
          }else {
            this.$message.error("")
          }
        })
      },
      show(row, index) {
        axios.post('http://localhost:8090/proxy',{tp: 'switchplatestatus',_id: row._id, status: 1}).then(resp => {
         if (resp.status == 200) {
           this.$message.success("")
           this.plates[index].status = 1
         }else {
           this.$message.error("")
         }
        })
      },
      addPlate() {
        this.dialogFormVisible = true
      },

      submitUpload(index) {
        switch (index) {
          case 10:
            this.$refs.newPlate.submit();
        }
      }
    },
    mounted() {
      axios.get('http://localhost:8090/plates').then(resp => {
        this.plates = resp.data.data
      })
    }
  }
</script>

<style>
  .el-row {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .el-col {
    border-radius: 4px;
  }

  .title {
    margin-top: 10px;
  }


</style>



